.hamburger--spin-r .hamburger-inner::after {
  transition: bottom 0.1s 0.34s ease-in, transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--spin-r.is-active .hamburger-inner {
  transform: rotate(-225deg);
  transition-delay: 0.14s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.hamburger--spin-r.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
  transition: top 0.1s ease-out, opacity 0.1s 0.14s ease-out;
}

.hamburger--spin-r.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(90deg);
  transition: bottom 0.1s ease-out, transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
}

/*
   * Squeeze
   */
.hamburger--squeeze .hamburger-inner {
  transition-duration: 0.1s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze .hamburger-inner::before {
  transition: top 0.1s 0.14s ease, opacity 0.1s ease;
}

.hamburger--squeeze .hamburger-inner::after {
  transition: bottom 0.1s 0.14s ease, transform 0.1s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze.is-active .hamburger-inner {
  transform: rotate(45deg);
  transition-delay: 0.14s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.hamburger--squeeze.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
  transition: top 0.1s ease, opacity 0.1s 0.14s ease;
}

.hamburger--squeeze.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(-90deg);
  transition: bottom 0.1s ease, transform 0.1s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
}

/*
   * Vortex
   */
.hamburger--vortex .hamburger-inner {
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.hamburger--vortex .hamburger-inner::before, .hamburger--vortex .hamburger-inner::after {
  transition-duration: 0s;
  transition-delay: 0.1s;
  transition-timing-function: linear;
}

.hamburger--vortex .hamburger-inner::before {
  transition-property: top, opacity;
}

.hamburger--vortex .hamburger-inner::after {
  transition-property: bottom, transform;
}

.hamburger--vortex.is-active .hamburger-inner {
  transform: rotate(765deg);
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.hamburger--vortex.is-active .hamburger-inner::before, .hamburger--vortex.is-active .hamburger-inner::after {
  transition-delay: 0s;
}

.hamburger--vortex.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
}

.hamburger--vortex.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(90deg);
}

/*
   * Vortex Reverse
   */
.hamburger--vortex-r .hamburger-inner {
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.hamburger--vortex-r .hamburger-inner::before, .hamburger--vortex-r .hamburger-inner::after {
  transition-duration: 0s;
  transition-delay: 0.1s;
  transition-timing-function: linear;
}

.hamburger--vortex-r .hamburger-inner::before {
  transition-property: top, opacity;
}

.hamburger--vortex-r .hamburger-inner::after {
  transition-property: bottom, transform;
}

.hamburger--vortex-r.is-active .hamburger-inner {
  transform: rotate(-765deg);
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.hamburger--vortex-r.is-active .hamburger-inner::before, .hamburger--vortex-r.is-active .hamburger-inner::after {
  transition-delay: 0s;
}

.hamburger--vortex-r.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
}

.hamburger--vortex-r.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(-90deg);
}

:root {
  --header-height: 3rem;
  --color-first: #007BDF;
  --color-second: #00CBFF;
  --color-third: #FF3C32;
  --color-accent: #FBA905;
  --color-dark: #282D31;
  --color-border: #DFE0E0;
  --color-light: #EAEAEA;
  --color-text-white: #FFFFFF;
  --color-text-gray: #53575A;
  --color-body-bg: #F6F6F6;
  --main-font: Lato;
  --alternate-font: sans-serif;
  --font-size: 16px;
  --line-height: 1.6;
}

@media screen and (min-width: 960px) {
  :root {
    --header-height: 4rem;
  }
}

html {
  box-sizing: border-box;
  font-family: var(--main-font), var(--alternate-font);
  font-size: var(--font-size);
  line-height: var(--line-height);
  color: var(--text-color);
  background-color: var(--bg-modal-color);
}

*,
*:after,
*:before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

::selection {
  background-color: var(--color-first);
  color: var(--color-text-white);
}

body {
  margin: var(--header-height) 0 0 0;
  min-height: calc(100vh - var(--header-height));
  background: var(--body-bg);
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.main-footer {
  background: var(--color-first);
  color: #FFF;
  padding: 2rem 0;
  font-size: fontSize(small);
  margin-top: auto;
}

.top-nav {
  border-bottom: 1px solid #DFE0E0;
  background: #FFF;
  position: fixed;
  top: 0;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  z-index: 4;
}

.top-nav__container {
  display: flex;
  justify-content: space-between;
  height: var(--header-height);
  width: 100%;
  max-width: 1200px;
  padding: 0 15px;
}

.top-nav__link {
  align-self: center;
}

.top-nav__logo {
  height: 2rem;
  width: auto;
  display: block;
}

@media screen and (min-width: 0) and (max-width: 959px) {
  .top-nav__menu {
    position: fixed;
    padding: 0 10px;
    height: 100vh;
    width: 260px;
    background: white;
    transition: right .5s;
    overflow-y: auto;
    border-top: 1px solid #DFE0E0;
    border-left: 1px solid #DFE0E0;
    top: var(--header-height);
    right: -260px;
  }
  .top-nav__menu.is-active {
    right: 0;
  }
}

@media screen and (min-width: 960px) {
  .top-nav__menu {
    align-self: center;
    margin: 0;
    padding: 0;
  }
}

.top-nav__li {
  margin-left: 1em;
  list-style-type: none;
  display: inline-block;
}

@media screen and (min-width: 0) and (max-width: 959px) {
  .top-nav__li {
    display: block;
    border-bottom: 1px solid #DFE0E0;
    padding: 10px 0;
    margin: 0;
  }
}

@media screen and (min-width: 0) and (max-width: 959px) {
  .top-nav__anchor {
    display: block;
  }
}

.top-nav__mb-iconMenu {
  position: relative;
  align-self: center;
  top: 3px;
  padding: 0;
}

.top-nav__mb-iconMenu span.hamburger-inner {
  background: black;
}

.top-nav__mb-iconMenu span.hamburger-inner::after, .top-nav__mb-iconMenu span.hamburger-inner::before {
  background: black;
}

.top-nav__mb-iconMenu:focus {
  outline: 0 !important;
  -moz-outline-style: none !important;
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

@media screen and (min-width: 960px) {
  .top-nav__mb-iconMenu {
    display: none;
  }
}

.bottom-nav {
  border-bottom: 1px solid #DFE0E0;
  background: #FFF;
  position: fixed;
  bottom: 0;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  z-index: -10;
}

.bottom-nav__container {
  display: flex;
  justify-content: space-between;
  height: 3rem;
  width: 100%;
  max-width: 1200px;
  padding: 0 15px;
}

.bottom-nav__menu {
  width: 100%;
  margin: 0;
  padding: 5px 0 0 0;
  display: flex;
  justify-content: space-between;
}

.bottom-nav__li {
  display: flex;
  justify-content: center;
}

.bottom-nav__anchor {
  color: black;
}

.bottom-nav__anchor span {
  margin-top: 5px;
  display: block;
  font-size: 14px;
}

.bottom-nav__anchor i {
  text-align: center;
  font-size: 20px;
  color: #007BDF;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 960px) and (-webkit-min-device-pixel-ratio: 2) {
  .bottom-nav {
    z-index: 1;
  }
}

.main-banner {
  color: var(--color-text-white);
  background: var(--color-first);
  --padding: 2rem;
  padding-top: var(--padding);
  padding-bottom: calc(var(--padding) * 1.25);
}

.main-banner.main-banner--secondary {
  background: var(--color-second);
}

.main-banner.main-banner--tertiary {
  background: var(--color-third);
}

.main-banner.main-banner--accent {
  background: var(--color-accent);
}

.main-banner.main-banner--dark {
  background: var(--color-dark);
}

.main-banner.diagonal {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 84%);
}

@media screen and (min-width: 960px) {
  .main-banner {
    --padding: 4rem;
  }
}

.main-banner__button {
  border: 1px solid var(--color-border);
}

.main-banner__button:hover {
  background: var(--color-accen);
  text-decoration: none;
}

.main-banner .main-banner__img {
  opacity: .3;
}

.main-banner .main-banner__data {
  position: relative;
}

.table-container {
  width: 100%;
  overflow-x: auto;
  border: 1px solid #DFE0E0;
  margin-bottom: 2em;
  position: relative;
}

.ed-table {
  table-layout: fixed;
  border-collapse: collapse;
  font-size: fontSize(small);
  background: white;
}

.ed-table th {
  background: #ccc;
  font-family: 'Open Sans', sans-serif;
  text-align: left;
  color: #FFF;
  font-weight: 700;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
}

.ed-table td {
  border-bottom: 1px solid rgba(204, 204, 204, 0.3);
  min-width: 100px;
}

.ed-table td:nth-child(even) {
  background: rgba(223, 224, 224, 0.2);
}

.ed-table tr:hover {
  background: rgba(204, 204, 204, 0.1);
}

.ed-table.first-column td:first-child {
  background: rgba(204, 204, 204, 0.75);
  color: #fff;
}

.ed-table td, .ed-table th {
  padding: 1em 1.5em;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.5em;
  margin-right: -0.5em;
}

.gallery img {
  display: block;
}

body {
  overflow-x: hidden;
}

.gallery > .gallery-item {
  width: calc(100% - 1em);
  margin: 0.5em;
}

@media screen and (min-width: 640px) {
  .gallery > .gallery-item {
    width: calc(50% - 1em);
  }
}

@media screen and (min-width: 960px) {
  .gallery > .gallery-item {
    width: calc(33.33333% - 1em);
  }
}

@media screen and (min-width: 1280px) {
  .gallery > .gallery-item {
    width: calc(25% - 1em);
  }
}

@supports (grid-template-areas: "header") {
  .gallery {
    display: grid;
    grid-gap: 1em;
    margin-left: 0;
    margin-right: 0;
    grid-template-columns: repeat(1, 1fr);
  }
  body {
    overflow-x: visible;
  }
  @media screen and (min-width: 640px) {
    .gallery {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media screen and (min-width: 960px) {
    .gallery {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  @media screen and (min-width: 1280px) {
    .gallery {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  .gallery > .gallery-item {
    width: 100%;
    margin: 0;
  }
}

/**
*   @fileoverview
*     Componente para incrustar un video de YouTube en nuestro sitio web cuando la resolucin es mayor a 64em (1024px), cuando es menor se agrega un enlace al video
*     Dependencias: font-awesome
*
*   @param {String} id, id del video de YouTube, se define en el archivo pug
*   @param {String} $video-width, anchura del contenedor del video, se define en el archivo scss
*
*   @returns {void} no retorna nada
*
*   @author Jonathan MirCha <jonmircha@gmail.com>
*   @version 1.0.0
*/
.Youtube {
  width: 100%;
  height: auto;
  display: block;
  margin: auto;
}

.Youtube-wrapper {
  position: relative;
  height: auto;
}

@media screen and (min-width: 64em) {
  .Youtube-wrapper {
    padding-bottom: 56.25%;
    /* 16:9 */
  }
}

.Youtube iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.Youtube a {
  display: block;
  margin: 2rem auto;
  text-align: center;
  text-decoration: none;
  font-size: 3.5vw;
}

.tabs-container {
  width: 100%;
  position: relative;
  margin: 5em auto;
}

.tabs-container__tabs {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.tabs-container__tab {
  background: #eee;
  color: #666;
  line-height: 2;
  padding: 0 1em;
  border: 1px solid #ccc;
  margin-right: 1px;
  border-bottom: none;
  border-radius: 4px 4px 0 0;
  position: relative;
  top: 1px;
  cursor: pointer;
}

.tabs-container__tab.is-active {
  background: #fff;
  color: #111;
  position: relative;
  z-index: 2;
}

.tabs-container__panels {
  position: relative;
  z-index: 1;
  border: 1px solid #ccc;
  padding: 1em;
}

.tabs-container__panel {
  position: absolute;
  left: -1000%;
}

.tabs-container__panel.is-active {
  position: relative;
  left: 0;
}

.accordion-container__btn-acc {
  background-color: #eee;
  border: none;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
}

.accordion-container .active {
  background-color: #ccc;
}

.accordion-container__panel {
  padding: 0 18px;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}

.accordion-container__btn-acc:after {
  content: '\02795';
  font-size: 13px;
  color: #777;
  float: right;
  margin-left: 5px;
}

.accordion-container .active:after {
  content: "\2796";
}

.tns-outer {
  position: relative;
}

.tns-outer .tns-controls {
  position: absolute;
  z-index: 1;
  width: 100%;
  top: 50%;
}

.tns-outer .tns-controls > button {
  position: absolute;
  transform: translateY(-50%);
  z-index: 100;
  background-color: transparent;
  border: 0;
  color: #FFF;
  font-size: 24px;
  opacity: 0.8;
  transition: opacity .3s ease;
}

.tns-outer .tns-controls > button:hover {
  opacity: 0.3;
}

.tns-outer .tns-controls > button:focus {
  outline: 0;
}

.tns-outer .tns-controls > button:first-child {
  left: 10px;
}

.tns-outer .tns-controls > button:last-child {
  right: 10px;
}

.tns-outer .tns-nav {
  position: absolute;
  z-index: 14;
  width: 100%;
  bottom: 0;
  text-align: center;
  margin: 10px 0;
}

.tns-outer .tns-nav [aria-controls] {
  width: 9px;
  height: 9px;
  padding: 0;
  margin: 0 5px;
  border-radius: 50%;
  background: #ddd;
  border: 0;
}

.tns-outer .tns-nav > .tns-nav-active {
  background: #999;
}

.tns-outer .tns-nav__nav-controls {
  position: absolute;
  width: 100%;
  text-align: center;
  margin: 10px 0;
  bottom: 0;
  z-index: 1000;
}

.tns-outer .tns-nav__nav-controls [aria-controls] {
  width: 20px;
  height: 20px;
  background: #717171;
  text-align: center;
  color: transparent;
  border-radius: 20px;
  border: 1px #aaa solid;
  position: relative;
  transition: all .25s;
  padding: 0;
  margin: 0 10px;
  cursor: pointer;
  outline: 0;
}

.tns-outer .tns-nav__nav-controls [aria-controls]:before {
  content: "";
  display: block;
  position: absolute;
  width: 18px;
  height: 18px;
  background-color: transparent;
  border: 2px solid transparent;
  border-radius: 20px;
  left: 0;
  top: 0;
}

.tns-outer .tns-nav__nav-controls [aria-selected="true"] {
  background-color: transparent;
}

.tns-outer .tns-nav__nav-controls [aria-selected="true"]:before {
  border-color: #fff;
}

